@each $breakpoint in map-keys($grid-breakpoints) {
  @include media-breakpoint-up($breakpoint) {
    $infix: breakpoint-infix($breakpoint);

    .float#{$infix}-left {
      @include float-left;
    }

    .float#{$infix}-none {
      @include float-none;
    }

    .float#{$infix}-right {
      @include float-right;
    }
  }
}



